import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { FormBuilder, FormGroup, Validators } from '@angular/forms';
import { first } from 'rxjs/operators';

import { AccountService, AlertService } from '@app/_services';

@Component({ templateUrl: 'add-edit.component.html' })
export class AddEditComponent implements OnInit {
    form: FormGroup;
    id: string;
    isAddMode: boolean;
    loading = false;
    submitted = false;

    constructor(
        private formBuilder: FormBuilder,
        private route: ActivatedRoute,
        private router: Router,
        private accountService: AccountService,
        private alertService: AlertService
    ) {}

    ngOnInit() {
        this.id = this.route.snapshot.params['id'];
        this.isAddMode = !this.id;
        
        // password not required in edit mode
        const passwordValidators = [Validators.minLength(6)];
        if (this.isAddMode) {
            passwordValidators.push(Validators.required);
        }

        this.form = this.formBuilder.group({
            name: ['', Validators.required],
            email: ['', Validators.required],
            phone: [''],
            age: [''],
            occupation:[''],
            caption:[''],
            address:[''],
            profilePicture:['']
        });

        if (!this.isAddMode) {
            this.accountService.getById(this.id)
                .pipe(first())
                .subscribe(data => {
                    let x=data.data
                    this.f.name.setValue(x.name);
                    this.f.email.setValue(x.email);
                    this.f.phone.setValue(x.phone);
                    this.f.age.setValue(x.age);
                    this.f.occupation.setValue(x.occupation);
                    this.f.caption.setValue(x.caption);
                    this.f.address.setValue(x.address);
                    this.f.profilePicture.setValue(x.profilePicture);
                });
        }
    }

    // convenience getter for easy access to form fields
    get f() { return this.form.controls; }

    onSubmit() {
        this.submitted = true;

        // reset alerts on submit
        this.alertService.clear();

        // stop here if form is invalid
        if (this.form.invalid) {
            return;
        }

        this.loading = true;
        if (this.isAddMode) {
            this.createUser();
        } else {
            this.updateUser();
        }
    }

    private createUser() {
        this.accountService.createUser(this.form.value)
            .pipe(first())
            .subscribe(
                data => {
                    this.alertService.success('User added successfully', { keepAfterRouteChange: true });
                    this.router.navigate(['.', { relativeTo: this.route }]);
                },
                error => {
                    this.alertService.error(error);
                    this.loading = false;
                });
    }

    private updateUser() {
        this.accountService.update(this.id, this.form.value)
            .pipe(first())
            .subscribe(
                data => {
                        this.alertService.success('Update successful', { keepAfterRouteChange: true });
                        this.router.navigate(['..', { relativeTo: this.route }]);
                        if(data.status == "success" && data.data && data.data.emailURL){
                            window.open(data.data.emailURL, "_blank");
                        }
                },
                error => {
                    this.alertService.error(error);
                    this.loading = false;
                });
    }

    onFileChange(event) {
  
        {
            const file = event.target.files[0];
          
            if (file) {
              const reader = new FileReader();
          
              reader.onload = this.handleReaderLoaded.bind(this);
              reader.readAsBinaryString(file);
            }
        }
}

handleReaderLoaded(e) {
    //this.base64textString.push('data:image/png;base64,' + btoa(e.target.result));
    console.log('data:image/png;base64,' + btoa(e.target.result))
    this.form.patchValue({
        profilePicture: 'data:image/png;base64,' + btoa(e.target.result)
    })
  }
}